@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');

* {
  font-family: 'Quicksand', sans-serif;
  color: #111;
  transition-property: color, background-color, stroke;
  transition: ease-in-out 500ms; 
}

body {
  margin: 0px;
  text-align: center;
}

#viewport {
  background: linear-gradient(145deg, #ccc, #aaa);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#timer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(145deg, white, #ccc);
  box-shadow: 5px 5px 10px #999, -5px -5px 10px #ddd;
  border: 1px solid #ccc;
  border-radius: 20px;
}

.title {
  margin: 10px;
}

.power {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-bottom: 50px;
}

.on {
  background: linear-gradient(145deg, #69DBA6, #026235);
  box-shadow: 5px 5px 10px #bbb, -5px -5px 10px white;
}

.off {
  background: linear-gradient(145deg, #ffa7a7, #cc1717);
  box-shadow: 5px 5px 10px #bbb, -5px -5px 10px white;
}

#inc_dec {
  display: flex;
  justify-content: center;
  align-items: center;
}

#display {
  border-radius: 30px;
  padding: 0 15px;
  box-shadow: 5px 5px 10px #ccc, -5px -5px 10px white;
}

#timer-label {
 margin: 5px 0 -13px 0;
}

#time-left {
  background: linear-gradient(0deg, #99ffff, #ccffff);
  border: 1px solid #ccc;
  padding: 0 10px;
  color: black;
  margin-bottom: 13px;
  border-radius: 10px;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.plus-minus-btn,
.button {
  background: linear-gradient(-145deg, #eee, #ddd);
  box-shadow: 5px 5px 10px #ccc, -5px -5px 10px white;
  border: 1px solid #ddd;
}

.button {
  border-radius: 20px;
  padding: 5px 10px;
  margin: 0px 10px;
}

.plus-minus-btn {
  border-radius: 55px;
  width: 25px;
  height: 25px;
  margin: 10px;
  text-align: center;
}